.footer {
  background-color: #252641;
  padding: 40px 20px;
}


.top {
  display: flex;
  justify-content: center;
  align-items: center;


		&__text {
      margin-left: 50px;
      color: #fff;
      font-size: 19px;
      font-weight: 500;
		}
}


.pic {
  max-width: 60px;
  border-right: 1px solid #626381;
  padding-right: 50px;
  box-sizing: content-box;
}

.bottom {
  max-width: 33%;
  margin: auto;

		&__text {
      text-align: center;
      color: #fff;
      font-size: 19px;
      margin: 0;
      margin-top: 60px;
      line-height: 30px;
		}

		&__conf {
      margin-top: 50px;
		}

		&__text-inf {
      text-align: center;
      color: #fff;
      font-weight: 600;
      font-size: 17px;
		}
}
