// базовые подключения
@use "settings";

// подключения компонентов страницы
@use "./components/header";
@use './components/footer';
@use './components/home';


html,
body {
	height: 100%;
}

body {
    width: 100%;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: #f7f8f8;

}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

footer {
    flex-shrink: 0;
    margin-top: auto;
}

main {
    display: flex;
	  flex-direction: column;
    margin: 50px auto;
    max-width: 1430px;
    padding-inline: 20px;
    box-sizing: border-box;
}

.global-text {
  font-size: 18px;
  line-height: 30px;
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.title {
  margin: 0;
  font-size: 40px;
  margin-bottom: 50px;
}

img {
  width: 100% !important;
  height: 100% !important;
}

.subtitle {
  font-size: 30px;
  margin-block: 40px;
}

.global-pic {
  width: 395px;
  height: 500px;
  overflow: hidden;
  border-radius: 5px;
  float: left;
  margin-right: 20px;
  margin-bottom: 10px;
  position: relative;

  img {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
