.header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 40px;
  gap: 20px;
  width: 100%;
  background-color: #e8eff7;
}

.header-pic {
  max-width: 70px;
}

.header-middle {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 50px;

	&__link {
    color: #5B5B5B;
    padding: 2px;
    font-size: 20px;
    transition: all .1s ease;
    cursor: pointer;

    @media (hover: hover) {
      &:hover {
        transform: scale(1.05);
        opacity: 0.9;
        color: red;
      }
    }
	}
}


.dropdown {
  position: relative;

  &__menu {
    position: absolute;
    left: 0;
    top: calc(100% + 10px);
    background-color: #70bfd3;
    border-radius: 5px;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;
    background-color: #fff7f7;
    max-height: 288.89px;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: visible;

    &::-webkit-scrollbar {
      width: 4px;
    }

    &::-webkit-scrollbar-thumb {
        background: rgb(0, 0, 0);
        border-radius: 10px;
    }
    &::-webkit-scrollbar-track {
        background-color: rgb(196, 195, 195);
    }


    &.active {
      visibility: visible;
      opacity: 1;
    }
  }

  &__btn {
    border: 0;
    background: none;
    outline: none;
  }

  &__top {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__pic {
    max-width: 15px;
    max-height: 15px;
    transition: all .2s ease;

    &.active {
      transform: rotate(180deg);
    }
  }

  &__text {
    font-size: 16px;
    display: block;
    color: black;
    padding: 20px;
    transition: all .2s ease-out;
    line-height: 20px;

    @media (hover: hover) {
      &:hover {
        background-color: #f1dddd;
      }
    }
  }
}


